.contacts {
  flex: 0 0 50%;
  padding-top: 56px;
  padding-bottom: 70px;
  padding-left: 30px;
  padding-right: 30px;
  @include vp-1199 {
  }

  @include vp-767 {
  }
}

.contacts__wrapper {
  width: 60%;
  margin: 0 auto;
  min-width: max-content;

  @include vp-1199 {
  }

  @include vp-767 {
  }
}

.contacts__title {
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  color: $color-sapphire;

  @include vp-1199 {
  }

  @include vp-767 {
  }
}

.contacts__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 30px;
  position: relative;

  @include vp-1199 {
  }

  @include vp-767 {
  }
}

.contacts__list::before {
  content: "";
  width: 1px;
  background-color: $color-lightSlateGrey;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);

  @include vp-1199 {
  }

  @include vp-767 {
  }
}

.contacts__item:nth-child(even) {
  padding-left: 60px;
}

.contacts__item:nth-child(odd) {
  padding-right: 60px;
}

.contacts__item h3 {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  color: $color-sapphire;

  @include vp-1199 {
  }

  @include vp-767 {
  }
}

.contacts__item a {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: $color-sapphire;
  @include vp-1199 {
  }

  @include vp-767 {
  }
}

.contacts__item p {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  @include vp-1199 {
  }

  @include vp-767 {
  }
}
